@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Шрифты */
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (min-width: 320px) and (max-width: 420px) {
  .advantages__wrapper::before {
    content: "";
    position: absolute;
    top: -16px;
    right: 0;
    width: 100%;
    height: 37px;
    background-image: url("../img/index/advantages-triangle.svg");
    background-position: center;
  }
}
@media (min-width: 320px) {
  .advantages__wrapper {
    position: relative;
  }

  .advantages__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
  }

  .advantages__item:nth-child(n+4)::before {
    content: "";
    position: absolute;
    background-color: #d6d6d6;
    max-width: 284px;
    width: 100%;
    height: 1px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
