  .advantages__item--gray {
    padding: 24px 31px;
    background-color: #eeeeee;
    position: relative;
  }

  .advantages__box {
    background-color: #81b3d2;
  }
  .advantages__box--img {
    padding: 54px 18px 40px 18px;
  }
  .advantages__box--img-reverse {
    padding: 37px 18px;
  }

  .advantages__image {
    display: block;
    object-fit: cover;
    width: 100%;
  }

  .advantages__title {
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    color: #000000;
    margin-bottom: 11px;
  }
  .advantages__title--img {
    color: #ffffff;
  }
  .advantages__title--img-reverse {
    color: #ffffff;
  }

  .advantages__number {
    font-size: 14px;
