    color: #ffffff;
  }
  .advantages__number--img-reverse {
    color: #ffffff;
  }

  .advantages__text {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    color: #333333;
    width: 100%;
