    background-image: url("../img/index/house.svg");
    background-repeat: no-repeat;
    max-width: 40px;
    width: 100%;
    height: 100%;
    background-size: contain;
  }
  .advantages__subitem--food::before {
    content: "";
    position: absolute;
    background-image: url("../img/index/food.svg");
    display: block;
    background-repeat: no-repeat;
    max-width: 40px;
    width: 100%;
    height: 100%;
    background-size: contain;
  }
  .advantages__subitem--souvenir::before {
    content: "";
    position: absolute;
    background-image: url("../img/index/souvenir.svg");
    display: block;
    background-repeat: no-repeat;
    max-width: 40px;
    width: 100%;
    height: 100%;
    background-size: contain;
  }

  .advantages__subtitle {
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    text-align: start;
    padding-left: 47px;
    color: #000000;
  }

  .advantages__subtext {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    padding-left: 47px;
    color: #333333;
    text-align: start;
  }
  .advantages__subtext br {
    display: none;
  }
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  .advantages__item--img {
    grid-column: 1/-1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .advantages__item--img-reverse {
    grid-column: 1/-1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .advantages__item--gray {
    padding: 41px 33px 41px 33px;
  }

  .advantages__box--img {
    padding: 52px 33px 31px 33px;
  }
  .advantages__box--img-reverse {
    padding: 52px 30px 31px 30px;
  }

  .advantages__image {
    grid-column: 2/-1;
  }
  .advantages__image--reverse {
    grid-column: 1/3;
    grid-row: 1/2;
  }

  .advantages__sublist {
    grid-column: 1/-1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0;
    padding: 0;
  }

  .advantages__subitem {
    align-items: center;
    gap: 24px;
  }
  .advantages__subitem--house {
    padding: 158px 33px 60px 33px;
  }
  .advantages__subitem--house::before {
    top: 60px;
    max-width: 77px;
    height: 72px;
  }
  .advantages__subitem--food {
    padding: 156px 33px 61px 33px;
  }
  .advantages__subitem--food::before {
    top: 62px;
    max-width: 81px;
    height: 68px;
  }
  .advantages__subitem--souvenir {
    padding: 153px 33px 64px 33px;
  }
  .advantages__subitem--souvenir::before {
    top: 64px;
    max-width: 67px;
    height: 75px;
  }

  .advantages__subtitle {
    margin: 0;
    font-size: 21px;
    padding-left: 0;
  }

  .advantages__subtext {
    padding-left: 0;
    text-align: center;
  }
  .advantages__subtext--house {
    max-width: 170px;
    width: 100%;
  }
  .advantages__subtext--food {
    max-width: 155px;
    width: 100%;
  }
  .advantages__subtext--souvenir {
    max-width: 165px;
    width: 100%;
  }

  .advantages__title {
    font-size: 21px;
    max-width: 155px;
    width: 100%;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 11px;
  }
  .advantages__title--gray {
    max-width: 155px;
    width: 100%;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 11px;
  }

  .advantages__text {
    text-align: center;
  }
  .advantages__text--gray br {
    display: block;
  }

  .advantages__number {
    text-align: center;
  }
  .advantages__number--gray {
    margin-bottom: 27px;
  }
}
