  }
  .form__group--contact {
    padding-bottom: 10px;
  }

  .form__list,
.form__radio-list,
.form__checkbox-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    list-style-type: none;
    gap: 16px;
    margin: 0;
    padding: 20px 0 24px 0;
  }

  .form__item {
    display: flex;
    flex-direction: column;
    position: relative;
  }
  .form__item--svg-tel::before {
    content: "";
    position: absolute;
    display: block;
    width: 58px;
    height: 58px;
    background-color: #e5e5e5;
    background-image: url("../img/form/icon-phone.svg");
    border: 2px solid transparent;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
    bottom: 0;
    right: 0;
  }
  .form__item--svg-email::before {
    content: "";
    position: absolute;
    display: block;
    width: 58px;
    height: 58px;
    background-color: #e5e5e5;
    background-image: url("../img/form/icon-email.svg");
    border: 2px solid transparent;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
    bottom: 0;
    right: 0;
  }

  .form__title {
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    margin-top: 24px;
  }

  .form__label-text {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    color: #333333;
    display: flex;
    margin-left: 16px;
  }

  .form__input-text {
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    background-color: #f2f2f2;
    text-align: start;
    text-transform: none;
    width: 100%;
    padding: 12px;
    border: 4px solid transparent;
  }
  .form__input-text:hover {
    background-color: #e5e5e5;
  }
  .form__input-text:focus {
    outline: none;
    background: #e5e5e5;
    border: 4px solid #b3b3b3;
  }
  .form__input-text:active {
    outline: none;
    background: #ffffff;
    border: 4px solid #e5e5e5;
  }

  .form__radio-label {
    position: relative;
    display: block;
    padding: 10px 0 10px 62px;
  }
  .form__radio-label .form__radio-mark:hover {
    background-color: #e5e5e5;
  }
  .form__radio-label .form__radio-mark:focus {
    outline: none;
    background-color: #d6d6d6;
  }
  .form__radio-label .form__radio-mark:active {
