    display: block;
    padding: 10px 0 10px 62px;
  }
  .form__checkbox-label .form__checkbox-mark:hover {
    background-color: #e5e5e5;
  }
  .form__checkbox-label .form__checkbox-mark:focus {
    outline: none;
    background-color: #d6d6d6;
  }
  .form__checkbox-label .form__checkbox-mark:active {
    outline: none;
    background-color: #b3b3b3;
