  height: 48px;
  background-color: #81b3d2;
}

.social__link {
  padding: 12px;
  color: #ffffff;
}
.social__link:hover {
  background-color: #669ec0;
}
.social__link:focus {
  outline: none;
  background: linear-gradient(180deg, #81b3d2 0%, #5496bd 100%);
}
.social__link:active {
  outline: none;
  background: linear-gradient(180deg, #f2f2f2 -165%, #81b3d2 45%);
}
.social__link:disabled {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}

.button {
  font-family: "PT Sans", "Arial", sans-serif;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #81b3d2;
  color: #ffffff;
  border: 4px solid transparent;
}

@media (min-width: 320px) {
  .page {
    height: 100%;
  }

  .page__body {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0 auto;
    font-family: "PT Sans", "Arial", sans-serif;
    font-size: 18px;
    line-height: 26px;
    font-weight: 400;
  }

  .page__container {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    width: 100%;
    padding: 0;
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .page__body {
    background-color: #f2f2f2;
  }

  .page__container {
    width: 100%;
    padding: 0;
    background-color: #ffffff;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}
@media (min-width: 1200px) {
  .page__body {
    background-color: #f2f2f2;
    max-width: 1200px;
  }

  .page__container {
    max-width: 1200px;
    width: 100%;
    padding: 0;
    background-color: #ffffff;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}